<template>
    <v-container>
        <!-- Main bar -->
        <!-- <v-row>
            <h1>GeoGIF Component</h1>
        </v-row> -->

        <v-row v-if="listFlag" justify="center" align="center">
            <!-- <v-col cols="9"> -->
                <v-text-field
                    v-model="searchGeoMet"
                    color="white"
                    class="mt-2"
                    style="margin: 0.5rem;"
                    label="Search GeoMet-Weather Layers"
                    rounded
                    filled>
                </v-text-field>
            <!-- </v-col> -->
            <!-- <v-spacer></v-spacer>
            <v-col cols="3" align-self="center">
                <v-btn
                    color="primary"
                    style="margin: 0.5rem;"
                    @click="addExampleLayers">
                    Add Example Layers
                    <v-icon>
                        mdi-map-plus
                    </v-icon>
                </v-btn>
            </v-col> -->
        </v-row>
        <v-row justify="center" v-if="!listFlag">
            <v-card loading>
                <v-card-title>
                Loading GeoMet-Weather Layers
                </v-card-title>
            </v-card>
        </v-row>
        <v-row>
            <v-sheet>
                <v-treeview
                    :items="filterObj"
                    :open.sync="open"
                    item-key="Title"
                    dense
                    class="overflow-y-auto overflow-x-auto"
                    style="max-height: 700px; min-width: 500px;"
                    ref="tree">
                    <template v-slot:prepend="{ item }">
                        <v-row align="center">
                        <v-col cols="auto">
                            <h3 v-if="item.children" class="text-no-wrap">{{ item.Title }}</h3>
                            <h4 v-if="!item.children" class="text-no-wrap">{{ item.Title }}</h4>
                            <v-divider v-if="!item.children"></v-divider>
                            <h6 v-if="!item.children" style="max-width: 600px;">{{ item.Dimension }}</h6>
                            <v-divider v-if="!item.children"></v-divider>
                            <h5 v-if="!item.children" style="max-width: 600px;">{{ item.Abstract }}</h5>
                        </v-col>
                        <v-col cols="auto" v-if="!item.children">
                            <v-btn x-large color="white" icon @click="addLayerEvent(item)" :disabled="clicked.includes(item.Name)">
                            <v-icon color="primary">
                                mdi-plus
                            </v-icon>
                            </v-btn>
                        </v-col>
                        </v-row>
                    </template>
                </v-treeview>
            </v-sheet>
        </v-row>
        <!-- GIF progress bar -->
        <v-row v-if="this.gifstarted">
            <v-progress-linear
                v-model="this.gifProgressPercent"
                height="20"
                rounded
                >
                <strong>{{ Math.ceil(this.gifProgressPercent) }}%</strong>
            </v-progress-linear>
        </v-row>
        <!-- Added layers chips -->
        <v-row v-if="addedGeoMetLayers.length !== 0" >
            <v-chip-group
                v-model="selectedChips"
                multiple
                active-class="primary--text"
                column
                @change="updateSelectedAddedGeoMetLayers"
            >
                <v-chip
                    v-for="(layerg, k) in addedGeoMetLayers"
                    :key="k"
                    :value="layerg"
                    style="chip-selected-opacity: 0.6;"
                    @click="chipClickEvent(layerg)"
                    >
                    {{ layerg.get('layerTitle') }}
                </v-chip>
            </v-chip-group>
        </v-row>
        <!-- Help Overlay -->
        <v-row align="center" justify="center" >
            <v-overlay
                :value="helpOverlay"
                opacity="0.3"
                z-index="10">
                <v-row>
                    <v-btn 
                        color="grey darken-2" 
                        block
                        class="mx-auto"
                        style="margin: 1rem;"
                        @click="updateHelpOverlayFlag">
                        Close Overlay
                    </v-btn>
                </v-row>
                <v-stepper
                    v-model="stepperData"
                    class="rounded-xl mx-auto"
                    style="max-width: 85%;"
                    vertical>
                    <v-stepper-step
                        :complete="stepperData > 1"
                        step="1"
                        >
                        Add Layers
                        <small>Initially there is just a Base Map</small>
                    </v-stepper-step>
                    <v-stepper-content step="1">
                        <v-card
                            class="mb-12"
                            height="200px">
                            <v-row justify="start" align="center" class="pa-4">
                                <v-btn
                                    color="primary"
                                    style="margin: 1rem;"
                                    >
                                    Add GeoMet Weather Layers
                                </v-btn>
                                <v-divider
                                    vertical
                                ></v-divider>
                                <span class="pl-4">
                                    Launches an overlay that allows you to search and pick layers.
                                </span>
                            </v-row>
                            <v-row justify="start" align="center" class="pa-4">
                                <v-btn
                                    color="primary"
                                    style="margin: 1rem;">
                                    Add Example Layers
                                    <v-icon>
                                        mdi-map-plus
                                    </v-icon>
                                </v-btn>
                                <v-divider
                                    vertical
                                ></v-divider>
                                <span class="pl-4">
                                    Adds 3 default layers to play around with.
                                </span>
                            </v-row>
                        </v-card>
                        <v-btn
                            color="primary"
                            @click="stepperData = 2"
                        >
                            Continue
                        </v-btn>
                    </v-stepper-content>

                    <v-stepper-step
                        :complete="stepperData > 2"
                        step="2">
                        Select Layers
                        <small>Sets the selected layers visible</small>
                    </v-stepper-step>
                    <v-stepper-content step="2">
                        <v-card class="mb-12">
                            <v-row justify="center" align="center" class="my-4 py-4">
                                <v-chip class="mx-4 px-4">
                                    Radar precipitation rate (Rain) (1 km) [mm/hr]
                                </v-chip>
                                <v-chip class="mx-4 px-4">
                                    Concentration: surface PM2.5 [kg/m3]
                                </v-chip>
                                <v-chip class="mx-4 px-4">
                                    RDWPS.SUPERIOR - Winds [m/s]
                                </v-chip>
                            </v-row>
                            <v-divider
                                class="ma-2"
                            ></v-divider>
                            <v-row class="pa-4">
                                <span class="pl-4 mx-auto">
                                    These "chips" represent the different layers added in Step 1. Click them to add and remove them from the map. For each selected layer a legend and control panel will appear.
                                </span>
                            </v-row>
                        </v-card>
                        <v-btn
                            color="primary"
                            @click="stepperData = 3"
                        >
                            Continue
                        </v-btn>
                        <v-btn
                            @click="stepperData = 1">
                            Previous
                        </v-btn>
                    </v-stepper-content>

                    <v-stepper-step
                        :complete="stepperData > 3"
                        step="3"
                        >
                        Controls and Information
                        <small>Legends, time sliders, export to GIF and more</small>
                    </v-stepper-step>
                    <v-stepper-content step="3">
                        <v-row justify="center" align="center" class="my-4 py-4">
                            <v-col cols="4">
                                <v-expansion-panels>
                                    <v-expansion-panel>
                                        <v-expansion-panel-header>
                                            Radar precipitation rate (Rain) (1 km) [mm/hr]
                                        </v-expansion-panel-header>
                                        <v-expansion-panel-content align="center">
                                            <v-img 
                                                src="https://geo.weather.gc.ca/geomet?version=1.3.0&service=WMS&request=GetLegendGraphic&sld_version=1.1.0&layer=RADAR_1KM_RRAI&format=image/png"
                                                max-height="200"
                                                max-width="150"
                                                contain></v-img>
                                        </v-expansion-panel-content>
                                    </v-expansion-panel>
                                </v-expansion-panels>
                            </v-col>
                            <v-col cols="7">
                                <span class="text-h5">
                                    For each Slected layer a panel with its title will appear. You can click the panel to view the legend of the layer. The example panel on the left is live so you can try it.
                                </span>
                            </v-col>
                        </v-row>
                        <v-divider
                            class="ma-2"
                        ></v-divider>
                        <v-row class="pa-4">
                            <h1>Rest of help</h1>
                        </v-row>
                    </v-stepper-content>
                </v-stepper>
            </v-overlay>
        </v-row>
        <!-- Legends panels for added and selected layers -->
        <v-row >
            <div ref="map" class="map" style="width: 100%; height: 500px;"></div>
        </v-row>
        <!-- Extra controls row -->
        <v-row justify="center" align="center" >
            <v-col cols="10">
                <v-btn
                    color="primary"
                    class="mt-12"
                    style="margin: 1rem;"
                    @click="addExampleLayers">
                    Add Example Layers
                    <v-icon>
                        mdi-map-plus
                    </v-icon>
                </v-btn>
            </v-col>
            <v-spacer></v-spacer>
            <v-col cols="2" align-self="center">
                <v-btn style="margin: 0.5rem;" color="green" @click="timeText = !timeText">
                    <v-icon>
                        mdi-clock-outline
                    </v-icon>
                    {{ this.timeText }}
                </v-btn>
                <v-btn style="margin: 0.5rem;" color="primary" @click="exportMap" fab>
                    <v-icon>
                        mdi-image-size-select-actual
                    </v-icon>
                </v-btn>
                <v-btn style="margin: 0.5rem;" color="primary" @click="updateHelpOverlayFlag" fab>
                    <v-icon>
                        mdi-help-circle
                    </v-icon>
                </v-btn>
            </v-col>
        </v-row>
        <!-- Controls for added and selected layers -->
        <v-row>
            <v-expansion-panels multiple style="padding-bottom: 150px;">
                <v-expansion-panel 
                    v-for="(ls,j) in getSelectedGeoMetLayers"
                    :key="j">
                    <v-expansion-panel-header>
                        {{ ls.get('layerTitle') }} : {{ opacities.find(l => {return l.layerName === ls.get('layerName')}).layerCurrentTime }}
                    </v-expansion-panel-header>
                    <v-expansion-panel-content>
                        <v-row>
                            <v-col cols="7">
                                <v-row>
                                    <v-slider 
                                        v-model="opacities.find(l => {return l.layerName === ls.get('layerName')}).layerOpacity"  
                                        min="0" 
                                        max="1" 
                                        step="0.05"
                                        style="margin: 0.5rem;" 
                                        @input="setOpacityGeneric(ls)">
                                        <template #prepend>
                                            Opacity
                                        </template>
                                        <template #append>
                                            {{ Math.ceil(Math.round(ls.getOpacity() * 100)) }}%
                                        </template>
                                    </v-slider>
                                    <v-btn icon x-large outlined style="margin: 0.5rem;" color="primary" @click="sonOfMapToGIF(ls)">
                                        <v-icon dark>
                                            mdi-file-gif-box
                                        </v-icon>
                                    </v-btn>
                                </v-row>
                                <h5 class="mt-4">
                                    {{ opacities.find(l => {return l.layerName === ls.get('layerName')}).layerAbstract }}
                                </h5>
                            </v-col>
                            <v-spacer></v-spacer>
                            <v-col cols="5">
                                <v-img 
                                    :src="`https://geo.weather.gc.ca/geomet?version=1.3.0&service=WMS&request=GetLegendGraphic&sld_version=1.1.0&layer=${ls.get('layerName')}&format=image/png`"
                                    max-height="300"
                                    max-width="200"
                                    style="margin: auto;"
                                    contain></v-img>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-slider
                                v-model="opacities.find(l => {return l.layerName === ls.get('layerName')}).layerSliderValue"
                                :max="getDateArrayLength(ls)"
                                ticks="always"
                                @input="sliderInputGeneric(ls)"
                                >
                                <template #prepend>
                                    {{ ls.get('layerStartTime') }}
                                </template>
                                <template #append>
                                    {{ ls.get('layerEndTime') }}
                                </template>
                            </v-slider>
                        </v-row>
                    </v-expansion-panel-content>
                </v-expansion-panel>
            </v-expansion-panels>
        </v-row>
        

        <a id="image-download" download="firework_experiment.png"></a>
        <a id="gif-download" download="firework_experiment.gif"></a>
    </v-container>
</template>

<script>
import Map from 'ol/Map';
import View from 'ol/View';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import Image from 'ol/layer/Image';
import ImageWMS from 'ol/source/ImageWMS';
import {fromLonLat} from 'ol/proj';
import GIF from 'gif.js';
import 'ol/ol.css'

import axios from 'axios'
import SaxonJS from 'saxon-js'

const parser = new DOMParser();

export default {
    async mounted() {
        this.getCapabilitiesGeoMet();
        this.map = new Map({
            target: this.$refs['map'],
            layers: [
                this.osm,
            ],
            view: new View({
                center: fromLonLat([-97, 43]),
                zoom: 4
            })
        });
        this.selectedChips = this.selectedAddedGeometLayers;
    },
    computed: {
        gifProgressPercent () {
            return Math.round((this.gifprogress / (this.giflength)) * 100)
        },
        listFlag () {
            return this.treeItems.length !== 0
        },
        filterObj () {
            return this.filterObjectFunction(this.treeItems, ({ Title }) => Title.toLowerCase().includes(this.searchGeoMet.toLowerCase()))
        },
        clicked () {
            return this.addedGeoMetLayers.map(o => o.get('layerName'))
        },
        getSelectedGeoMetLayers () {
            return this.selectedGeometLayers
        }
    },
    methods: {
        updateGeoMetLayerFlag: function () {
            if (!this.addGeoMetLayerOverlay) {
                this.getCapabilitiesGeoMet();
            }
            this.addGeoMetLayerOverlay = !this.addGeoMetLayerOverlay;
        },
        updateHelpOverlayFlag() {
            this.helpOverlay = !this.helpOverlay;
        },
        updateSelectedAddedGeoMetLayers: function () {
            this.selectedGeometLayers = this.selectedChips;
        },
        async addLayerEvent(layer) {
            const data = this.getStartEndTimeNew(layer.Dimension);
            // const data = await this.getStartEndTime(layer.Name);
            let tempLayer = new Image({
                                            source: new ImageWMS({
                                                format: "image/png",
                                                url: "https://geo.weather.gc.ca/geomet/",
                                                params: { LAYERS: layer.Name},
                                                transition: 0,
                                                crossOrigin: "Anonymous"
                                                }),
                                            visible: false,
                                            opacity: 0.7,
                                            zIndex: 10
                                        })
            tempLayer.setProperties({   'layerName': layer.Name,
                                        'layerTitle': layer.Title,
                                        'layerStartTime': data[0],
                                        'layerEndTime': data[1],
                                        'layerTimeStep': this.convertTimeStepToInt(data[2])})
            
            var tempDateArray = await this.getDateArray(data[0],data[1],data[2]);
            this.opacities.push({   'layerName': layer.Name,
                                    'layerAbstract': layer.Abstract,
                                    'layerOpacity': 0.7,
                                    'layerCurrentTime': data[0],
                                    'layerDateArray': tempDateArray,
                                    'layerDateArrayLength': tempDateArray.length, 
                                    'layerAnimationID': null,
                                    'layerSliderValue': 0})
            this.map.addLayer(tempLayer)
            this.addedGeoMetLayers.push(tempLayer);
        },
        setTimeGeneric: function(layer) {
            var c = this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerCurrentTime"]
            var s = layer.get('layerStartTime')
            var e = layer.get('layerEndTime')
            var t = layer.get('layerTimeStep')

            if (c === null) {
                c = s;
            } else if (c >= e) {
                c = s;
            } else {
                c = new Date(this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerCurrentTime"].setMinutes(c.getMinutes() + t));
            }
            layer.getSource().updateParams({ TIME: c.toISOString().split(".")[0] + "Z" });
        },
        resetCurrentTime(layer) {
            this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerCurrentTime"] = layer.get('layerStartTime');
            var tempc = this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerCurrentTime"];
            layer.getSource().updateParams({ TIME: tempc.toISOString().split(".")[0] + "Z" });
        },
        async setTimeDate(layer,dateToSet) {
            this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerCurrentTime"] = dateToSet;
            layer.getSource().updateParams({ TIME: dateToSet.toISOString().split(".")[0] + "Z"})
        },
        myTimeout(ms) {
            return new Promise(resolve => setTimeout(resolve, ms));
        },
        async playGeneric(layer) {
            var da1 = this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerDateArray"]
            var tn = layer.get('layerName')
            this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerAnimationID"] = true;
            for (let i = 0 ; i < da1.length - 1 ; i++) {
                if (!this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerAnimationID"]) {
                    break;
                }
                this.setTimeDate(layer,da1[i]);
                for (let j = 0 ; j < this.getSelectedGeoMetLayers.length - 1 ; j++) {
                    if (tn !== this.getSelectedGeoMetLayers[j].get('layerName')) {
                        var tda = this.opacities.find(la => {return la.layerName === this.getSelectedGeoMetLayers[j].get('layerName')})["layerDateArray"]
                        for (let k = 0 ; k < tda.length - 1 ; k++) {
                            if (da1[i].getTime() === tda[k].getTime()) {
                                this.setTimeDate(this.getSelectedGeoMetLayers[j],tda[k]);
                            }
                        }
                    }
                }
                Promise.all([await new Promise(resolve => this.map.once('rendercomplete', resolve)),await this.myTimeout(1000)])
            }
        },
        pauseGeneric(layer) {
            if (this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerAnimationID"] !== null) {
                this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerAnimationID"] = false;
            }
        },
        async getDateArray(start,end,step) {
            var dateArray = new Array();
            var s = start;
            var e = end;
            var i = this.convertTimeStepToInt(step);
            while (s <= e) {
                dateArray.push(s);
                s = new Date( s.setMinutes( s.getMinutes() + i ) );
            }
            return dateArray;
        },
        getDateArrayLength (layer) {
            return this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerDateArrayLength"]
        },
        sliderInputGeneric: function(layer) {
            this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerCurrentTime"] = this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerDateArray"][this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerSliderValue"]]
            this.setTimeGeneric(layer);
        },
        getStartEndTimeNew (layerDimension) {
            var data = layerDimension.split("/");
            return [new Date(data[0]), new Date(data[1]), data[2]];             
        },
        async getStartEndTime(layerName) {
            let url = "https://geo.weather.gc.ca/geomet/?lang=en&service=WMS&request=GetCapabilities&version=1.3.0&LAYERS=" + layerName;
            let response = await fetch(url);
            let data = await response
                .text()
                .then((data) =>
                parser
                    .parseFromString(data, "text/xml")
                    .getElementsByTagName("Dimension")[0]
                    .innerHTML.split("/")
                );
            return [new Date(data[0]), new Date(data[1]), data[2]];
        },
        setOpacityGeneric: function(layer) {
            layer.setOpacity(this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerOpacity"]);
        },
        async chipClickEvent(layer) { 
            layer.setVisible(!layer.getVisible());
        },
        convertTimeStepToInt: function(timestep) {
            var stepint;
            switch(timestep) {
                case "PT10M":
                    stepint = 10;
                    break;
                case "PT15M":
                    stepint = 15;
                    break;
                case "PT30M":
                    stepint = 10;
                    break;
                case "PT1H":
                    stepint = 60;
                    break;
                case "PT3H":
                    stepint = 180;
                    break;
                case "PT6H":
                    stepint = 360;
                    break;
                case "PT12H":
                    stepint = 720;
                    break;
                case "PT24H":
                    stepint = 1440;
                    break;
            }
            return stepint;
        },
        // AddGeometLayer Functions
        filterObjectFunction: function (array, fn) {
            return array.reduce((r, o) => {
                var children = this.filterObjectFunction(o.children || [], fn)
                if (fn(o) || children.length) r.push(Object.assign({}, o, children.length && { children }))
                return r
            }, [])
        },
        getCapabilitiesGeoMet: function () {
            axios.get('https://geo.weather.gc.ca/geomet?lang=en&service=WMS&version=1.3.0&request=GetCapabilities').then((response) => {
                const xslt =
                `<xsl:stylesheet 
                xmlns:xsl="http://www.w3.org/1999/XSL/Transform"
                xmlns:xs="http://www.w3.org/2001/XMLSchema"
                xmlns:mf="http://example.com/mf"
                exclude-result-prefixes="#all"
                xpath-default-namespace="http://www.opengis.net/wms"
                xmlns="http://www.w3.org/2005/xpath-functions"
                expand-text="yes"
                version="3.0">

                <xsl:strip-space elements="*"/>
                
                <xsl:mode on-no-match="shallow-skip"/>

                <xsl:output method="json" build-tree="no" indent="yes"/>
                
                <xsl:template match="/WMS_Capabilities/Capability/Layer" priority="5">
                    <xsl:map>
                    <xsl:apply-templates/>
                    </xsl:map>
                </xsl:template>
                
                <xsl:template match="Layer/Title[1] | Layer/Name[1] | Layer/Abstract[1] | Layer/Dimension[1]">
                    <xsl:map-entry key="local-name()" select="data()"/>
                </xsl:template>
                
                <xsl:template match="Layer[1]">
                    <xsl:map-entry key="'children'">
                    <xsl:sequence select="array { ../Layer/mf:apply-templates(.) }"/>
                    </xsl:map-entry>
                </xsl:template>
                
                <xsl:template match="Layer[position() > 1]"/>
                
                <xsl:function name="mf:apply-templates" as="item()*">
                    <xsl:param name="elements" as="element(*)*"/>
                    <xsl:map>
                    <xsl:apply-templates select="$elements/*"/>      
                    </xsl:map>
                </xsl:function>
                
                </xsl:stylesheet>`
                const jsonResult = SaxonJS.XPath.evaluate(`
                transform(
                    map { 
                    'source-node' : parse-xml($xml), 
                    'stylesheet-text' : $xslt, 
                    'delivery-format' : 'raw' 
                    }
                    )?output`,
                [],
                { 'params': { 'xml': response.data, 'xslt': xslt } }
                )
                this.treeItems = jsonResult.children
                this.geometRoot = jsonResult.Title
            })
        },
        exportMap: function () {
            var tempTimeText = this.timeText;
            this.map.once('rendercomplete', function () {
                const mapCanvas = document.createElement('canvas');
                const divElement = document.querySelector(".map");
                mapCanvas.width = divElement.offsetWidth;//size[0];
                mapCanvas.height = divElement.offsetHeight;//size[1];
                const mapContext = mapCanvas.getContext('2d');
                Array.prototype.forEach.call(
                document.querySelectorAll('.ol-layer canvas'),
                function (canvas) {
                    if (canvas.width > 0) {
                    const opacity = canvas.parentNode.style.opacity;
                    mapContext.globalAlpha = opacity === '' ? 1 : Number(opacity);
                    const transform = canvas.style.transform;
                    const matrix = transform
                                            .match(/^matrix\(([^\(]*)\)$/)[1] //eslint-disable-line
                                            .split(',')
                                            .map(Number);
                    CanvasRenderingContext2D.prototype.setTransform.apply( mapContext , matrix );
                    mapContext.drawImage(canvas, 0, 0);
                    }
                }
                );
                mapContext.drawImage(mapCanvas,0,0,100,100);
                console.log(tempTimeText);
                if(tempTimeText) {
                    var text = 'Last try before I start looking for almond flavored powder';
                    mapContext.strokeStyle = 'black';
                    mapContext.fillStyle = 'black';
                    mapContext.textAlign = 'center';
                    mapContext.font = '32px Airal';
                    mapContext.fillText(text, divElement.offsetWidth / 2, divElement.offsetHeight / 2);
                }

                if (navigator.msSaveBlob) {
                    navigator.msSaveBlob(mapCanvas.msToBlob(), 'firework_experiment.png');
                } else {
                    const link = document.getElementById('image-download');
                    console.log(typeof mapCanvas);
                    console.log(typeof mapContext);
                    link.href = mapCanvas.toDataURL();
                    link.click();
                }
            });
            this.map.renderSync();
        },
        async sonOfMapToGIF(layer) {
            var tempDateArray = this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerDateArray"];
            var dal = this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerDateArrayLength"];
            var tn = layer.get('layerName')
            this.giflength = dal;
            this.gifstarted = true;
            var tempgif = new GIF({
                workers: 4,
                quality: 20,
                height: this.map.getSize()[1],
                width: this.map.getSize()[0],
                workerScript: process.env.VUE_APP_PUBLIC_PATH_EN + '/gif.worker.js'
            });

            for (let i = 0 ; i < tempDateArray.length - 1 ; i++) {
                this.setTimeDate(layer,tempDateArray[i]);
                for (let j = 0 ; j < this.getSelectedGeoMetLayers.length - 1 ; j++) {
                    if (tn !== this.getSelectedGeoMetLayers[j].get('layerName')) {
                        var tda = this.opacities.find(la => {return la.layerName === this.getSelectedGeoMetLayers[j].get('layerName')})["layerDateArray"]
                        for (let k = 0 ; k < tda.length - 1 ; k++) {
                            if (tempDateArray[i].getTime() === tda[k].getTime()) {
                                this.setTimeDate(this.getSelectedGeoMetLayers[j],tda[k]);
                            }
                        }
                    }
                }
                await new Promise(resolve => this.map.once('rendercomplete', resolve));
                this.mapToGIFCallback(tempgif);
                this.gifprogress++;
            }
            tempgif.on('finished', function(blob) {
                if (navigator.msSaveBlob) {
                    navigator.msSaveBlob(blob, 'firework_experiment.gif');
                } else {
                    window.open(URL.createObjectURL(blob));
                }
            });
            tempgif.render();
            this.gifstarted = false;
            this.gifprogress = 0;
            this.giflength = 0;
            tempgif = null;
            this.resetCurrentTime(layer);
        },
        mapToGIFCallback(gif) {
            const mapCanvas = document.createElement('canvas');
            const divElement = document.querySelector(".map");
            mapCanvas.width = divElement.offsetWidth;//size[0];
            mapCanvas.height = divElement.offsetHeight;//size[1];
            const mapContext = mapCanvas.getContext('2d');
            Array.prototype.forEach.call(
                document.querySelectorAll('.ol-layer canvas'),
                function (canvas) {
                    if (canvas.width > 0) {
                        const opacity = canvas.parentNode.style.opacity;
                        mapContext.globalAlpha = opacity === '' ? 1 : Number(opacity);
                        const transform = canvas.style.transform;
                        const matrix = transform
                                                .match(/^matrix\(([^\(]*)\)$/)[1] //eslint-disable-line
                                                .split(',')
                                                .map(Number);
                        CanvasRenderingContext2D.prototype.setTransform.apply(mapContext,matrix);
                        mapContext.drawImage(canvas, 0, 0);
                    }
                }
            );
            gif.addFrame(mapCanvas, {copy:true, delay: this.delayInput});
        },
        addExampleLayers() {
            this.addLayerEvent(this.mockRain);
            this.addLayerEvent(this.mockFire);
            this.addLayerEvent(this.mockSuperior);
        },
    },
    data () {
        return {
            map: null,
            gif: null,
            gifstarted: false,
            gifprogress: 0,
            giflength: 0,
            osm: new TileLayer({source: new OSM()}),
            surface10: new Image({
                                    source: new ImageWMS({
                                                format: "image/jpeg",
                                                url: "https://geo.weather.gc.ca/geomet/",
                                                params: { LAYERS: "RAQDPS-FW.SFC_PM10"},
                                                transition: 0,
                                                crossOrigin: "Anonymous"
                                                }),
                                    opacity: 0.7,
                                    visible: true
                                    }),
            startTimeSurface10: null,
            endTimeSurface10: null,
            currentTimeSurface10: null,
            dateArraySurface10: null,
            animationIDSurface10: null,
            sliderSurface10: 0,
            opacitySurface10: 0.7,
            s10ToggleFlag: true,
            frameRate: 1.0,
            helpOverlay: false,
            stepperData: 1,
            helpOpacity: 0.7,
            helpDateArray: [],
            helpDate: Date.now(),
            // Data Related to Add GeoMet Layers
            addedGeoMetLayers: [],
            selectedAddedGeometLayers: [],
            addGeoMetLayerOverlay: false,
            searchGeoMet: "",
            treeItems: [],
            open: [],
            geometRoot: '',
            opacities: [],
            selectedGeometLayers: [],
            selectedChips: [],
            delayInput: 200,
            timeText: false,
            mockRain: {
                Name: 'RADAR_1KM_RRAI',
                Title: 'Radar precipitation rate (Rain) (1 km) [mm/hr]',
                Abstract: 'Composite of American and Canadian weather radars updated every 10 minutes',
                Dimension: '2022-02-16T15:40:00Z/2022-02-16T18:40:00Z/PT10M'
            },
            mockFire: {
                Name: 'RAQDPS.SFC_PM2.5',
                Title: 'Concentration: surface PM2.5 [kg/m3]',
                Abstract: 'The Regional Air Quality Deterministic Prediction System (RAQDPS) carries out physics and chemistry calculations to arrive at deterministic predictions of chemical species concentration of interest to air quality. Chemical constituents include O3, SO2, NO, and NO2 gases as well as fine particulate matter PM2.5 (2.5 micrometers in diameter or less) and coarse particulate matter PM10 (10 micrometers in diameter or less). Predictions are available from the current day out to 48 hours into the future. Geographical coverage is Canada and the United States. Data is available at a horizontal resolution of 10 km. While the system encompasses 80 vertical levels, data is available only for the surface level. Predictions are performed twice a day.',
                Dimension: '2022-02-16T12:00:00Z/2022-02-19T12:00:00Z/PT1H'
            },
            mockSuperior: {
                Name: 'RDWPS-Lake-Ontario_1km_Winds_10m',
                Title: 'RDWPS - Winds at 10m above surface [m/s]',
                Abstract: 'The Regional Deterministic Wave Prediction System (RDWPS) carries out physics calculations to arrive at deterministic predictions of wave conditions in oceans and large bodies of water from the current day out to 48 hours into the future. Forecast elements include significant wave height, swell, peak period and others. This product contains raw numerical results of these calculations. Geographical coverage is specific to marine areas over the Pacific, Atlantic and Arctic oceans as well as over the Great Lakes. Data is only available for the surface level. Data is available at varying horizontal resolutions from 5 to 50 km. Predictions are performed four times a day.',
                Dimension: '2022-02-16T12:00:00Z/2022-02-18T12:00:00Z/PT1H'
            }
        }
    }
}
</script>

