<template>
  <v-app>
    <v-main>
      <v-banner
        color="red lighten-4"
        v-model="banner"
        single-line>
        <v-row justify="center" align="center">
          <v-col>
            <v-card color="error" class="ma-4 pa-4">
              <span class="text-h5 white--text text-center ma-8">
                Please enable popups for this website in order to be able to export the GIF files.
              </span>
            </v-card>
          </v-col>
        </v-row> 
        <template v-slot:actions="{ dismiss }">
          <v-btn
            x-large
            text
            color="error"
            @click="dismiss"
          >
            Dismiss
          </v-btn>
        </template> 
      </v-banner>
      <router-view/>
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: 'GeoGIF',
  data: () => ({
    banner: true
  })
};
</script>

<style>
.no-uppercase {
     text-transform: unset !important;
}

::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar:hover {
  width: 15px;
}
/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #99ddff;
  border-radius: 10px;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #00aaff;
}
</style>

